.events-all{
    height: 100%;
    width: 100%;
    overflow-x: hidden;
}

.options-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #141921;
    padding: 10px;

    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    z-index: 10;
}

.events-all__list{
    height: 100%;
    width: 100%;
    padding: 0 10px;
    margin-top: 70px;
}

.events-all__item{
    width: 100%;
    height: calc(100% / 3);
    background-color: #141921;
    margin-bottom: 15px;
}

.events-all__item:last-child{
    margin-bottom: 0;
}

@media screen and (min-width:768px){
    .events-all__list{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .events-all__item{
        width: calc((100% - 40px) / 3);
        height: 300px;
    }
}