.event-page{
    height: 100%;
    width: 100%;
    position: relative;
    color: #fff;
    overflow: hidden;
    background: url(../../assets/map.png) center center no-repeat;
    background-size: cover;
}

.event-page__head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding: 0 15px;
}

.event-page__content{
    padding: 0 15px;
    height: 100%;
    margin-top: 20px;
}

.event-page__image{
    height: 200px;
    width: 100%;
    overflow: hidden;
}

.event-page__image-pic{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.event-page__title{
    font-size: 20px;
    font-weight: 500;
    margin-top: 15px;
}

.event-page__bar{
    background-color: #141921;
    margin-top: 30px;
    color: #fff;

    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px 10px;

    box-shadow: 0 0 25px #fff;
}

.event-page__bar-date, .event-page__bar-time{
    font-size: 20px;
    display: inline-flex;
    align-items: center;
}

.event-page__bar-date{
    margin-right: 30px;
}

.event-page__bar-date::before,.event-page__bar-time::before{
    content:'';
    display: inline-block;
    height: 24px;
    width: 24px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-right: 10px;
}

.event-page__bar-date::before{
    background: url(../../assets/calendar.png);
}

.event-page__bar-time::before{
    background: url(../../assets/clock.png);
}

.event-page__info{
    margin-top: 30px;
}

.prev__button-img , .options__button-img{
    max-width: 25px;
    max-height: 20px;
}

.event-page__access{
    display: inline-flex;
    align-items: center;
    margin-top: 10px;
}

.event-page__access-name{
    margin-right: 17px;
    font-size: 19px;
}

.event-page__buy-ticket{
    position: fixed;
    bottom: 20px;
    left: 15px;
    right: 15px;
}

@media screen and (min-width:768px){
    .event-page__content{
        max-width: 940px;
        margin: 20px auto 0;
    }

    .event-page__buy-ticket{
        max-width: 400px;
        margin: 0 auto;
    }
}