.mgo-widget-call_pulse {
    height: 14px;
    width: 14px;
    background-color: #a41354;
    border-radius: 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    opacity: .75;
}
.mgo-widget-call_pulse:after,
.mgo-widget-call_pulse:before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    top: 0;
    border-radius: 100%;
    -webkit-animation: pulse 1.2s infinite ease-in-out;
    animation: pulse 1.2s infinite ease-in-out;
}
.mgo-widget-call_pulse:before {
    background: -webkit-linear-gradient(left,  rgba(66,34,87,1) 0%,rgba(60,35,86,1) 27%,rgba(99,39,70,1) 58%,rgba(100,34,75,1) 100%);
    background: linear-gradient(to right,  rgba(66,34,87,1) 0%,rgba(60,35,86,1) 27%,rgba(99,39,70,1) 58%,rgba(100,34,75,1) 100%);

    margin: auto 0 auto -15px;
    height: 30px;
    width: 30px;
    opacity: .7;
    -webkit-animation-delay: .6s;
    animation-delay: .6s;
}
.mgo-widget-call_pulse:after { 
    background: -webkit-linear-gradient(-45deg,  rgba(46,33,67,1) 0%,rgba(50,72,94,1) 15%,rgba(29,57,61,1) 35%,rgba(28,58,35,1) 57%,rgba(66,31,56,1) 100%); 
    background: -webkit-linear-gradient(315deg,  rgba(46,33,67,1) 0%,rgba(50,72,94,1) 15%,rgba(29,57,61,1) 35%,rgba(28,58,35,1) 57%,rgba(66,31,56,1) 100%); 
    background: linear-gradient(135deg,  rgba(46,33,67,1) 0%,rgba(50,72,94,1) 15%,rgba(29,57,61,1) 35%,rgba(28,58,35,1) 57%,rgba(66,31,56,1) 100%); 

    margin: auto 0 auto -25px;
    height: 50px;
    width: 50px;
    -webkit-animation-delay: .2s;
    animation-delay: .2s;
    opacity: .5;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 0;
  }
}

.events{
    position: fixed;
    z-index: 2;
    left: 5px;
    right: 5px;
    bottom: -100%;
    height: 200px;
    max-width: 100%;
    -webkit-transition: all .7s;
    transition: all .7s;
    margin:0 10px;
    border-radius: 7px;
    box-shadow: 0 0 17px #2e1f40;
    background-color: #1b232c;
}

.events__picture{
    height:70%;
    width: 100%;
    position: relative;
    overflow: hidden;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
}

.events__picture-img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.events__block-head{
    position: absolute;
    left: 0;
    top: 0;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    margin: 10px 10px 0 10px;

    color: #fff;
    font-size: 20px;
    font-weight: 400;
}

.events__block-info{
    height: 30%;
    display: flex;
    align-items: center
}

.events__block-time{
    font-size: 18px;
}


.events__block-title{
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    margin-left: 10px;
}


.vision{
    -webkit-animation: test .7s ease-in alternate forwards;
            animation: test .7s ease-in alternate forwards;
}


@-webkit-keyframes test {
    0%{
        bottom: -100%;
    }

    100%{
        bottom: 78px;
    }
}


@keyframes test {
    0%{
        bottom: -100%;
    }

    100%{
        bottom: 78px;
    }
}

@media screen and (min-width:768px){
