.login{
    height: 100%;
    width: 100%;
    background: url(../../assets/wave1.png) center bottom no-repeat;
    background-size: contain;
    position: relative;
    padding: 0 5%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#51256d+0,202e35+62,171d26+83,141c24+99 */
    
}

.login::before{
    content:'';
    width: 100%;
    height: 100%;
    z-index: -1;
    position: absolute;
    left: 0;
    top: 0;
    background: -moz-linear-gradient(top,  rgba(81,37,109,1) 0%, rgba(32,46,53,1) 62%, rgba(23,29,38,1) 83%, rgba(20,28,36,1) 99%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(81,37,109,1) 0%,rgba(32,46,53,1) 62%,rgba(23,29,38,1) 83%,rgba(20,28,36,1) 99%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(81,37,109,1) 0%,rgba(32,46,53,1) 62%,rgba(23,29,38,1) 83%,rgba(20,28,36,1) 99%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.login__wrapp{
    height: 100%;
    width: 100%;
    text-align: center;

    display: flex;
    flex-direction: column;
    justify-content: center; 
    align-items: center;
    
}

.login__form{
    width: 100%;
}

.login__create{
    position: absolute;
    bottom: 0;
    margin-bottom: 20px;
    color: #fff;
}



.form{
    display: flex;
    flex-direction: column;
}

.form__label{
    margin-bottom: 15px;
}

.form__input{
    width: 100%;
    background-color: #fff;
    font-weight: 500;
    font-size: 18px;
    padding: 10px 10px;
    border-radius: 7px;
}

.form__input::placeholder{
    color:#52256c;
}

.form__button{
   display: flex;
   flex-direction: column;
   align-items: center;
   margin-top: 15px;
}

.form__button .button--under{
    align-self: flex-end;
}

.logo__link{
    color: #fff;
    text-decoration: none;
}

@media screen and (min-width:768px){
    .login__wrapp{
        max-width: 690px;
        margin: 0 auto;
    }
}