.menu-bar{
    position: fixed;
    bottom: 0;
    z-index: 3;
    left: 0;
    right: 0;
    width: 100%;
    height: 8vh;
}

.menu-bar__list{
    display: flex;
    align-items: center;
    height: 100%;
}

.menu-bar__item{
    flex:1;
    height: 100%;
    text-align: center;
    border-right: 1px solid grey;
    display: inline-flex;
    align-items: center;
}

.menu-bar__item:last-child{
    border-right: none;
}

.menu-bar__link{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .4s;
    background-color: blue;
}

.menu-bar__link:hover, .menu-bar__link:focus{
    background-color: #4848ed;
}