@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap');

*{
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
}

body,html{
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

ul,li{
  list-style: none;
  padding: 0;
  margin: 0;
}

#root{
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}

input{
  outline: none;
  border:1px solid transparent;
}

.mb-none{
  margin-bottom: 0 !important;
}

button{
  outline: none;
  border: 1px solid transparent;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

a{
  text-decoration: none;
  color: #fff;
}

.button{
  color: #fff;
  font-weight: 500;
  font-size: 18px;
  text-transform: uppercase;
  padding-bottom: 2px;
}


.button--submit{
  background: -moz-linear-gradient(-45deg,  rgba(82,37,109,1) 0%, rgba(113,42,144,1) 21%, rgba(171,39,118,1) 57%, rgba(193,41,135,1) 78%, rgba(216,40,125,1) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg,  rgba(82,37,109,1) 0%,rgba(113,42,144,1) 21%,rgba(171,39,118,1) 57%,rgba(193,41,135,1) 78%,rgba(216,40,125,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg,  rgba(82,37,109,1) 0%,rgba(113,42,144,1) 21%,rgba(171,39,118,1) 57%,rgba(193,41,135,1) 78%,rgba(216,40,125,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  padding: 10px 0;
  border-radius: 10px;
  width: 100%;
  margin-top: 25px;
}

.button--under{
  background-color: transparent;
  border-bottom: 1px solid #fff;
  display: inline-block;
  font-weight: 300;
  font-size: 13px;
  text-transform: none;
}

.button--follow{
  background-color:#44b098;
  color: #fff;
  padding: 7px 15px;
  border-radius: 37px;
  text-transform: none;
  max-width: 115px;
  width: 100%;
}

.button__buy-ticket{
  width: 100%;
  padding: 10px 5px;
  background-color: red;
  color: #fff;
  font-size: 17px;
  font-weight: 400;
}

.options__button{
  position: relative;
}

.options__list{
  width: 170px;
  padding: 5px;
  position: absolute;
  right: 10px;
  background-color: #fff;
}

.options__item{
  padding: 5px 0;
  color: black;
  font-size: 16px;
  margin-bottom: 10px;
  cursor: pointer;
}

.options__item:hover{
  color: blue;
}

.options__item:last-child{
  margin-bottom: 0;
}

.container{
  max-width: 940px;
  margin: 0 auto;
  height: 100%;
}