.map{
    height: 100%;
    width: 100%;
}

.map__icon{
    display: inline-block;
    max-width: 100%;
    max-height: 100%;
}

.map__button{
    display: inline-block;
    height: 25px;
    width: 25px;
    outline: none;
    background-color: transparent;
    border: none;
    cursor: pointer;
    position: relative;
}

.map__popup{
    color: black;
}

.map__popup-access{
  margin-top: 6px;
}

.mgo-widget-call_pulse {
    height: 14px;
    width: 14px;
    background-color: #a41354;
    border-radius: 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
    opacity: .75;
}
.mgo-widget-call_pulse:after,
.mgo-widget-call_pulse:before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    top: 0;
    border-radius: 100%;
    -webkit-animation: pulse 1.2s infinite ease-in-out;
    animation: pulse 1.2s infinite ease-in-out;
}
.mgo-widget-call_pulse:before {
    background: -moz-linear-gradient(left,  rgba(66,34,87,1) 0%, rgba(60,35,86,1) 27%, rgba(99,39,70,1) 58%, rgba(100,34,75,1) 100%);
    background: -webkit-linear-gradient(left,  rgba(66,34,87,1) 0%,rgba(60,35,86,1) 27%,rgba(99,39,70,1) 58%,rgba(100,34,75,1) 100%);
    background: linear-gradient(to right,  rgba(66,34,87,1) 0%,rgba(60,35,86,1) 27%,rgba(99,39,70,1) 58%,rgba(100,34,75,1) 100%);

    margin: auto 0 auto -15px;
    height: 30px;
    width: 30px;
    opacity: .7;
    -webkit-animation-delay: .6s;
    animation-delay: .6s;
}
.mgo-widget-call_pulse:after {
    background: -moz-linear-gradient(-45deg,  rgba(46,33,67,1) 0%, rgba(50,72,94,1) 15%, rgba(29,57,61,1) 35%, rgba(28,58,35,1) 57%, rgba(66,31,56,1) 100%); 
    background: -webkit-linear-gradient(-45deg,  rgba(46,33,67,1) 0%,rgba(50,72,94,1) 15%,rgba(29,57,61,1) 35%,rgba(28,58,35,1) 57%,rgba(66,31,56,1) 100%); 
    background: linear-gradient(135deg,  rgba(46,33,67,1) 0%,rgba(50,72,94,1) 15%,rgba(29,57,61,1) 35%,rgba(28,58,35,1) 57%,rgba(66,31,56,1) 100%); 

    margin: auto 0 auto -25px;
    height: 50px;
    width: 50px;
    -webkit-animation-delay: .2s;
    animation-delay: .2s;
    opacity: .5;
}

@-webkit-keyframes pulse {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
