.profile-page .events-all__list{
    margin-top: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.profile-page .events-all__item{
    width: calc((100% - 10px) / 2);
    height: 250px;
}

.profile-page .events__block-title{
    font-size: 18px;
}

.profile-page__header{
    min-height: 30vh;
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    color: #fff;
    padding: 0 15px;
}

.profile-page__title{
    color:black;
    font-size: 30px;
    padding: 0 10px;
    margin-bottom: 5px;
    text-align: center;
}

.profile-page__account{
    margin-top: 52px;
}

.profile-page__header:after{
    content:'';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background:rgb(44, 27, 27,.4);
}

.profile-page__row{
    position: relative;
    z-index: 3;
    text-align: center;
}

.profile-page__personal{
    font-size: 26px;
}

.profile-page__content{
    margin-top: 23px;
}

.profile-page__city{
    font-size: 22px;
    color: rgb(242, 198, 198);
}

.profile-page__row{
    margin-top: 25px;
}

.profile-page__row:last-child{
    margin-top: 0;
    margin-bottom: 20px;
}

.profile-page__info{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.profile-page__avatar{
    text-align: center;
    z-index: 2;
    position: relative;
    margin-top: -58px;
}

.profile-page__avatar-icon{
    display: inline-block;
    height: 115px;
    width: 115px;
    border: 1px solid transparent;
    border-radius: 50%;
    overflow: hidden;
}

.profile-page__avatar-icon_picture{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.profile-page__button{
    margin-top: -20px;
}

.profile-page__followers, .profile-page__following{
    font-size: 30px;
}

.profile-page__subtitle{
    color: rgb(242, 198, 198);
    font-size: 15px;
}

.profile-page__descr{
    color: rgb(213, 171, 171);
    font-size: 18px;
    text-align: center;
    padding: 0 10px;
    margin-bottom: 10px;
}

