.events{
    position: fixed;
    z-index: 2;
    left: 5px;
    right: 5px;
    bottom: -100%;
    height: 200px;
    max-width: 100%;
    transition: all .7s;
    margin:0 10px;
    border-radius: 7px;
    box-shadow: 0 0 17px #2e1f40;
    background-color: #1b232c;
}

.events__picture{
    height:70%;
    width: 100%;
    position: relative;
    overflow: hidden;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
}

.events__picture-img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.events__block-head{
    position: absolute;
    left: 0;
    top: 0;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    margin: 10px 10px 0 10px;

    color: #fff;
    font-size: 20px;
    font-weight: 400;
}

.events__block-info{
    height: 30%;
    display: flex;
    align-items: center
}

.events__block-time{
    font-size: 18px;
}


.events__block-title{
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    margin-left: 10px;
}


.vision{
    animation: test .7s ease-in alternate forwards;
}


@keyframes test {
    0%{
        bottom: -100%;
    }

    100%{
        bottom: 78px;
    }
}

@media screen and (min-width:768px){
    .events{
        max-width: 570px;
        margin: 0 auto;
    }
}